.map {
  height: 550px;
  background-color: #333042;
  padding: 1rem;
  border-radius: 10px;
  margin-top: 3%;
  box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}
.map .leaflet-container {
  height: 100%;
}
.info-flag {
  height: 80px;
  width: 100%;
  background-size: cover;
  border-radius: 8px;
}
.info-flag img {
  width: 100px;
  border-radius: 5px;
}
.info-name {
  font-size: 20px;
  font-weight: bold;
  color: #555;
}
.info-confirmed,
.info-recovered,
.info-deaths {
  font-size: 16px;
  margin-top: 5px;
}
.info-container {
  width: 150px;
}

@media (max-width: 845px) {
  .map {
    height: 450px;
  }
}

@media (max-width: 750px) {
  .map {
    height: 375px;
  }
}
